import React from 'react';
import { Styled } from './styled';

interface Props {
  avatar?: string;
  name: string;
  lastMessage?: string;
}

export const UserInfo = ({ avatar, name, lastMessage }: Props) => {
  return (
    <Styled.UserInfo>
      <Styled.Avatar src={avatar} name={name} />
      <Styled.DialogName>
        <Styled.UserName>{name}</Styled.UserName>
        <Styled.LastMessage>
          {lastMessage || 'Start messaging...'}
        </Styled.LastMessage>
      </Styled.DialogName>
    </Styled.UserInfo>
  );
};
