import React from 'react';
import { Styled } from './styled';
import { UserInfo } from './UserInfo';
import { MessageInfo } from './MessageInfo';
import { IActionUser } from '../../store/User';
import { store } from '../../store';
import { deleteDialog, parseError } from '../../helpers';
import moment from 'moment';
interface Props {
  from: IActionUser;
  lastMessage?: string;
  read: boolean;
  dialogId: string;
  timeOfLastMessage: number;
}

export const Dialog = ({
  from,
  lastMessage,
  read,
  dialogId,
  timeOfLastMessage,
}: Props) => {
  const deleteHandler: React.MouseEventHandler = async (e) => {
    e.preventDefault();

    try {
      await deleteDialog(dialogId);
    } catch (error) {
      store.setError(parseError(error));
    }
  };

  return (
    <Styled.Dialog unread={!read}>
      <UserInfo
        name={from.name}
        avatar={from.userAvatar}
        lastMessage={lastMessage}
      />
      <Styled.MessageActions>
        {lastMessage && (
          <MessageInfo
            time={moment(timeOfLastMessage).fromNow()}
            unread={read}
          />
        )}
        <Styled.DeleteDialog>
          <span onClick={deleteHandler}>&times;</span>
        </Styled.DeleteDialog>
      </Styled.MessageActions>
    </Styled.Dialog>
  );
};
